import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Img from "gatsby-image";

export default function Article({ data }) {
  return (
    <Layout>
      <div>
        <h1 className="articletitle">{data.markdownRemark.frontmatter.title}</h1>
        <div className="article">
          <div className="summary">
            <p>{data.markdownRemark.frontmatter.summary}</p>
          </div>
          <div className="tools">
            <p>Built with:</p>
            <p className="toolname">{data.markdownRemark.frontmatter.tools}</p>
            <p>My role:</p>
            <p className="toolname">{data.markdownRemark.frontmatter.role}</p>
          </div>
          <Img fluid={data.markdownRemark.frontmatter.heroimg.childImageSharp.fluid} className="heroimg"/>  
        </div>
        
        <div className="articlecontent">
          <div dangerouslySetInnerHTML={{ __html: data.markdownRemark.html }} />
        </div>
      </div>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        role
        summary
        tools
        heroimg {
          childImageSharp {
            fluid(maxWidth: 1080,quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
      
    }
  }
`